import React, { Component } from 'react'
import Helmet from 'react-helmet'
import './index.css';
import Link from 'gatsby-link'

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    color: 'white',
    display: 'flex',
    flex: 1,
    alignItems: 'center',
  },
  rocket: {
  },
  flexify: {
  },
  form: {
    paddingTop: 40,
  },
};

class Landing extends Component {
  state = {
    subscribed: false,
  };

  renderForm() {
    if (this.state.subscribed) {
      return (
        <div
          className="col-12 col-sm-7"
        >
          <div
            style={{paddingBottom: 10}}
            className="form-group"
            style={styles.form}
          >
            <i className="fa fa-check-circle fa-fw fa-1x" style={{ color: '#4BB543', paddingRight: 10}}></i>
            Subscribed
          </div>
        </div>
      );
    } else {
      return (
        <form
          className="col-12 col-sm-7"
          id="form"
          style={styles.form}
          onSubmit={(e) => {
            const email = e.target.elements['email'].value;
            const resetForm = () => {
              document.getElementById('form').reset();
            }

            fetch('https://fu4yuz4nk8.execute-api.ap-southeast-1.amazonaws.com/default/chinloongtan-web-lambda', {
              method: 'POST',
              body: JSON.stringify({ email: email }),
              mode: 'no-cors',
            }).then(() => {
              resetForm();
              this.setState({ subscribed: true });
            }).catch(() => {
              resetForm();
            });

            e.preventDefault();
          }}
        >
          <div className="form-group">
            <input type="email" name="email" className="" id="emailInput" aria-describedby="emailHelp" placeholder="me@email.com" />
          </div>
          <button type="submit" className="submit-btn">Notify Me</button>
        </form>
      );
    }
  }

  render() {
    return (
      <div className="background container-fluid" style={styles.container}>
        <Helmet>
          <script>
            {`
            `}
          </script>
        </Helmet>

        <div className="content" style={styles.content}>
          <div className="row content-row" style={styles.flexify}>
            <div className="col-12 col-sm-7 col-md-7" style={styles.rocket}>
              <div
                style={{
                  background: 'transparent',
                  // marginBottom: '1.45rem',
                }}
              >
                <div
                  style={{
                    margin: '0 auto',
                    // maxWidth: 960,
                    paddingTop: 20,
                    paddingBottom: 20,
                    // padding: '1.45rem 1.0875rem',
                  }}
                >
                  <h1 style={{ margin: 0 }}>
                    <Link
                      to="/"
                      style={{
                        color: 'white',
                        textDecoration: 'none',
                      }}
                    >
                      React Native Push Start
                    </Link>
                  </h1>
                </div>
              </div>
            </div>
            <div className="col-12 col-sm-7 col-md-7" style={styles.flexify}>
              <div>
                Get started building apps with React Native.
              </div>
              <div>
                From web to mobile app developer.
              </div>
              <div>
                It has to start somewhere.
              </div>
              <div>
                (Coming soon)
              </div>
            </div>

            {this.renderForm()}
          </div>
        </div>
      </div>
    );
  }
}

export default Landing;




